import React, { useEffect, useState } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import axios from 'axios';

function Login() {
  const { currentUser, login, handleAuthCallback } = useAuth();
  const navigate = useNavigate();
  const location = useLocation();

  const [manualProvider, setManualProvider] = useState('openai');
  const [manualApiKey, setManualApiKey] = useState('');
  const [loadingManual, setLoadingManual] = useState(false);

  useEffect(() => {
    if (currentUser) {
      navigate('/');
      return;
    }
  
    // Check for hash-based parameters first (for OAuth callbacks)
    const hash = location.hash.substring(1);
    const hashParams = new URLSearchParams(hash);
    const code = hashParams.get('code');
    const provider = hashParams.get('provider');
  
    // If not in hash, check in regular query params
    const queryParams = new URLSearchParams(location.search);
    const queryCode = queryParams.get('code') || code;
    const queryProvider = queryParams.get('provider') || provider;
  
    if (queryCode && queryProvider) {
      handleAuthCallback(queryProvider, queryCode)
        .then(() => {
          navigate('/');
        })
        .catch((error) => {
          console.error('Authentication error:', error);
        });
    }
  }, [currentUser, navigate, location.search, location.hash, handleAuthCallback]);

  const handleLogin = (provider) => {
    login(provider).catch((error) => {
      console.error('Login error:', error);
    });
  };

  const handleManualSubmit = async () => {
    try {
      setLoadingManual(true);
      console.log('Attempting manual login with:', {
        provider: manualProvider,
        apiKey: manualApiKey ? '****' : 'none'
      });
      
      const response = await axios.post('/api/auth/set_api_key', {
        provider: manualProvider,
        api_key: manualApiKey || null,
      });
      
      console.log('Manual login response:', response.data);
      
      const token = response.data.token;
      if (!token) {
        throw new Error('No token returned');
      }
      
      localStorage.setItem('token', token);
      axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
      navigate('/');
    } catch (err) {
      console.error('Manual login failed', err);
      alert(`Login failed: ${err.response?.data?.error || err.message}`);
    } finally {
      setLoadingManual(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 flex flex-col justify-center py-12 sm:px-6 lg:px-8">
      <div className="sm:mx-auto sm:w-full sm:max-w-md">
        <div className="flex justify-center">
          <span className="text-4xl">🎓</span>
        </div>
        <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900">
          ChatHarvard
        </h2>
        <p className="mt-2 text-center text-sm text-gray-600">
          Your Personal Academic Advisor for Harvard University
        </p>
      </div>

      <div className="mt-8 sm:mx-auto sm:w-full sm:max-w-md">
        <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
          <div className="space-y-6">
            <div>
              <p className="text-sm font-medium text-gray-700 mb-4 text-center">
                Sign in with your account
              </p>
              <div className="mt-6 grid grid-cols-1 gap-4">
                <button
                  onClick={() => handleLogin('anthropic')}
                  className="group relative w-full flex justify-center py-3 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-harvard-crimson hover:bg-harvard-dark focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-harvard-crimson"
                >
                  <span className="flex items-center">
                    <span className="mr-2">🤖</span>
                    Continue with Anthropic
                  </span>
                </button>

                <button
                  onClick={() => handleLogin('openai')}
                  className="group relative w-full flex justify-center py-3 px-4 border border-gray-300 text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-harvard-crimson"
                >
                  <span className="flex items-center">
                    <span className="mr-2">🔍</span>
                    Continue with OpenAI
                  </span>
                </button>
              </div>
            </div>

            {/* Manual API Key Entry */}
            <div className="mt-8">
              <p className="text-sm font-medium text-gray-700 mb-2 text-center">
                Or use your own API key
              </p>
              <select
                value={manualProvider}
                onChange={(e) => setManualProvider(e.target.value)}
                className="w-full mb-3 p-2 border rounded-md"
              >
                <option value="openai">OpenAI (GPT-4)</option>
                <option value="anthropic">Anthropic (Claude)</option>
              </select>
              <input
                type="text"
                placeholder="Paste your API key or leave blank"
                value={manualApiKey}
                onChange={(e) => setManualApiKey(e.target.value)}
                className="w-full mb-3 p-2 border rounded-md"
              />
              <button
                onClick={handleManualSubmit}
                disabled={loadingManual}
                className="w-full bg-harvard-crimson text-white p-2 rounded-md hover:bg-red-700"
              >
                {loadingManual ? 'Signing in...' : 'Continue with API Key'}
              </button>
            </div>
          </div>

          <div className="mt-10">
            <div className="rounded-md bg-gray-50 p-4">
              <div className="flex">
                <div className="flex-shrink-0">
                  <svg className="h-5 w-5 text-gray-400" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true">
                    <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-8-3a1 1 0 00-.867.5 1 1 0 11-1.731-1A3 3 0 0113 8a3.001 3.001 0 01-2 2.83V11a1 1 0 11-2 0v-1a1 1 0 011-1 1 1 0 100-2zm0 8a1 1 0 100-2 1 1 0 000 2z" clipRule="evenodd" />
                  </svg>
                </div>
                <div className="ml-3">
                  <p className="text-sm text-gray-700">
                    Sign in to access personalized academic recommendations based on your profile and course history.
                  </p>
                </div>
              </div>
            </div>
          </div>

        </div>
      </div>
    </div>
  );
}

export default Login;
